<template>
    <div class="content centered padding-15">
        <div class="pannel">
            <div class="header is-clipped fixed is-alert">   
                {{ name }}         
            </div>
            <div class="pannel-img">
                <img :src="image">
            </div>
            <div class="body">
                <p id="text">{{ description }}</p>
            </div>
            <button class="button is-info">Project details</button>
        </div>
    </div>
</template>

<script>
import { Modal } from './index.js'
export default {
    name: 'project',
    props: ['name', 'description', 'link', 'image'],
    data () {
        return {
            modal_class: 'modal'
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.content {
    max-width: 25%;
}

.body {
    color: #000;
}

#text {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.button {
    margin-top: 10px;
    margin-bottom: 20px;
    cursor: pointer;
}


</style>




